

















































.container {
	width: 100%;
	height: 100vh;
	background: #f7f7f7;
	overflow: auto;
}

.banner {
	height: 375px;
	margin: 20px;
	box-sizing: border-box;
	background-color: #eee;
	line-height: 0;
	border-radius: 10px;
	overflow: hidden;
	box-shadow: 2px 2px 2px 1px #ddd;

	>img {
		width: 100%;
		height: 100%;
	}
}



.section {
	background-color: #fff;
	margin: 0 20px 20px;
	border-radius: 10px;
	padding: 20px;
	box-shadow: 2px 2px 2px 1px #ddd;
	

	span {
		font-size: 28px;
		line-height: 2em;
		color: #666;
	}
}
